.chatbox {
//   margin:10px;
  font-size: 22px;
//   background-color: rgba(111, 0, 255, 0.176);
//   height: 100vh;
  .chats {
    margin: 10px;
    // flex-direction: column;
    margin-bottom: 4.5rem;
    margin-top: 1.5rem;
    // display: flex;
    // width: 100%;
    .messagewrap {
        // width: 1100%;
      display: flex;
      align-items: right;
      border:blue;
    //   flex-wrap: wrap;
    //   border:solid;
      justify-content:right;

      .message {
        // background-color: wheat;
        padding: 7px;
        border-radius: 10px;
        border: solid;
        border-width: 2px;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 3px;
        .messagetext {
          // width:100px;
          overflow: hidden;
          max-width: 250px;
          display: flex;
          // border:solid;
        }
      }
    }
  }
  .sendbox {
    position: fixed;
    bottom: 0px;
    width: 100vw;
    // height:2rem;
    // background: wheat;
    // margin:10px;
    margin-bottom: 0;
    padding: 3px;
    form {
      margin: 0.3rem;
      // width: 100%;
      // height:100%;
      // margin-bottom: 0;
      display: flex;
      justify-content: space-between;
      .textarea {
        width: 85%;
        border-radius: 10px;
        padding: 5px;
        height: 3rem;
      }
      button {
        border: solid;
        // width:7%;
        padding: 3px;
        padding-left: 10px;
        padding-right: 10px;
        border-radius: 20px;
        margin-left: 10px;
        margin-right: 10px;
        height: 3rem;
      }
    }
  }
}
@media (min-width: 768px) {
  .chatbox {
      font-size: 25px;
    .chats {
        margin-top: 4.5rem;
      .messagewrap {
       

        .message {
          
          .messagetext {
            
            max-width: 550px;
            
          }
        }
      }
    }

    .sendbox {
      form {
        .textarea {
          width: 94%;
        }
      }
    }
  }
}
